<template>
	<div v-bind:class=" (imgParentClass) ? imgParentClass : null">
		<img :src="getImage" :alt="imageAlt">
	</div>
</template>

<script>

export default {
	name: "AtomImage",
	props: {
		imgParentClass: {
			type: String,
			required: false,
		},
		imageSrc: {
			type: String,
			required: true,
			default: ""
		},
		imageAlt: {
			type: String,
			required: false,
			default: ""
		},
	},
	computed: {
		getImage: {
			get() {
				if (this.isURLValid(this.imageSrc)) {
					return this.imageSrc;
				}

				return require("../../assets/img/" + (this.imageSrc || "placeholder.png"));
			}
		}
	},
	data() {
		return {
			img: require("../../assets/img/" + (this.imageSrc || "placeholder.png"))
		}
	},
	methods: {
		isURLValid: function (url) {
			const regexp = new RegExp( '(?:(?:https?|ftp|file):\\/\\/|www\\.|ftp\\.)(?:\\([-A-Z0-9+&@#\\/%=~_|$?!:,.]*\\)|[-A-Z0-9+&@#\\/%=~_|$?!:,.])*(?:\\([-A-Z0-9+&@#\\/%=~_|$?!:,.]*\\)|[A-Z0-9+&@#\\/%=~_|$])', 'i');
			return regexp.test(url);
		}
	}
}
</script>

