<script>

import {store} from "@/store/store";
import PayplugBancontactCountry from "@/classes/PaymentMethods/PaymentOptions/PayplugBancontactCountry";

export default class PayplugBancontact {

  type = null;
  name = null;
  title = null;
  image = null;
  checked = null;
  description = null;
  link_know_more = null;
  test_mode_avail = false;
  options = [];

  constructor(data) {
    const mode = store.manageMode();
    let that = this;
    this.type = data.type
    this.name = data.name
    this.title = data.title
    this.image = data.image
    this.checked = data.checked
    this.description = data.descriptions[mode].description;
    this.link_know_more = data.descriptions[mode].link_know_more;

    if (typeof data.available_test_mode !== "undefined") {
      this.test_mode_avail = data.available_test_mode;
    }

    if (typeof data.options !== "undefined") {
      data.options.map((payment_options_data) => {
        const payment_options = handlePaymentOptions(payment_options_data);
        if (payment_options !== null) {
          that.options.push(payment_options)
        }
      });
    }

    function handlePaymentOptions(data) {
      switch (data.name) {
        case 'bancontact_country':
          return new PayplugBancontactCountry(data);
          // eslint-disable-next-line no-unreachable
          break;
        default:
          return null;
          // eslint-disable-next-line no-unreachable
          break;
      }
    }
  }

}

</script>
