import axios from "axios";
import {store} from "@/store/store";
import PayplugHeader from "../classes/PayplugHeader";
import PayplugLogin from "../classes/PayplugLogin";
import PayplugSubscribe from "../classes/PayplugSubscribe";
import PayplugLogged from "../classes/PayplugLogged";
import PayplugPaylater from "../classes/PayplugPaylater";
import PayplugPaymentMethods from "../classes/PayplugPaymentMethods";
import PayplugStatus from "../classes/PayplugStatus";
import PayplugDebugMode from "../classes/PayplugDebugMode";
import PayplugFooter from "../classes/PayplugFooter";

export async function $post(url, data) {
    // eslint-disable-next-line no-useless-catch
    try {
        return axios.post(url, data);

    } catch (error) {
        throw error;
    }
}

export function $init() {
    $post(
        store.settings.ajax_url + process.env.VUE_APP_INIT, {}
    ).then((response) => {
        store.settings.logged = response.data.data.settings.logged;

		let check_var = typeof response.data.data.settings.psaccount === "undefined" || response.data.data.settings.psaccount === null;
		store.settings.psaccount = check_var ? true : response.data.data.settings.psaccount;

        store.settings.mode = response.data.data.settings.mode;
        store.settings.show_register = response.data.data.settings.show_register;
        populate(response.data.data);

        store.rawData = response.data.data;
    })
        .catch(error => {
            //commit('setError', error);
            console.log(error);
        })
        .finally(function () {
            //commit('setLoading', false);
            store.settings.loading.global = false;
        })
}

export function $login() {

    let data = {
        "action": "payplug_login",
        "payplug_email": store.data.payplug_email,
        "payplug_password": btoa(store.data.payplug_password)
    };

    // Reset the login error before trying to login a second time
    store.error.login = null;

    $post(
        store.settings.ajax_url + process.env.VUE_APP_LOGIN, data
    ).then((response) => {
        if (response.data.success === false) {
            const {...errorObject} = response;
            store.error.login = errorObject.data.data.message;
        } else {
            store.settings.logged = response.data.data.settings.logged;
            store.settings.mode = response.data.data.settings.mode;
            populate(response.data.data);
            store.rawData = response.data.data;
        }

    }).catch(function (error) {
        const {response} = error;
        const {...errorObject} = response; // take everything but 'request'
        store.error.login = errorObject.data.data.message;

    }).finally(function () {
        store.settings.loading.login = false;
    })

}

export function $logout() {

    $post(
        store.settings.ajax_url + process.env.VUE_APP_LOGOUT, {}
    ).then((response) => {
        store.settings.logged = response.data.data.settings.logged;
        store.settings.mode = response.data.data.settings.mode;
        populate_subscribe(response.data.data.subscribe);
        populate_status(response.data.data.status);
    }).catch(function (error) {
        console.log(error);

    }).finally(function () {
        store.settings.logged = false;
        store.settings.loading.logout = false;
    })

}

export function $refresh_keys() {

    let data = {
        "payplug_email": store.rawData.payplug_wooc_settings.email,
        "payplug_password": btoa(store.data.payplug_password),
        "env": store.settings.mode,
    };
    store.error.refresh_keys = null;
    store.error_inactive_alert = false;
    $post(
        store.settings.ajax_url + process.env.VUE_APP_REFRESH_KEYS, data
    ).then((response) => {
        if (response.data.success === false) {
            if (response.data.data.still_inactive) {
                store.error_inactive_alert = true;
                window.scrollTo({top: 100, behavior: 'smooth'});
            } else {
                const {...errorObject} = response;
                store.error.refresh_keys = errorObject.data.data.message;
            }
        } else {
            store.active_alert = true;
            store.translations.logged.inactive_modal.inactive = false;
            store.error_inactive = false;
            document.getElementById("payplug_sandbox0").click();
        }
    }).catch(function (error) {
        const {response} = error;
        const {...errorObject} = response; // take everything but 'request'
        store.error.refresh_keys = errorObject.data.data.message;

    }).finally(function () {
        store.settings.loading.logout = false;
    })
}

