export function $check_requirements() {
    let timer = -1;
    clearInterval(timer);
    $post(
        store.settings.ajax_url + process.env.VUE_APP_REQUIREMENTS, {}
    ).then((response) => {
        populate_status(response.data.data.status);
        if (response.data.data.status.error) {
            // Disable the plugin if a requirement error exist
            document.getElementById("payplug_enable-0").click();
            document.getElementById("payplug_enable").classList.add("-disabled");
        } else {
            // Enable the plugin if a no requirement error exist
            document.getElementById("payplug_enable-1").click();
            document.getElementById("payplug_enable").classList.remove("-disabled");
        }

    }).catch(function (error) {
        console.log(error);

    }).finally(function () {
        store.settings.loading.status = false;
        if (!store.translations.status.error) {
            store.success_check = true;
            timer = setInterval(() => {
                store.success_check = false;
            }, 5000);
        }
    })

}

export async function $save() {

    store.data.action = 'payplug_save_data';
    store.settings.save.disabled = true;
    store.settings.loading.global = true;

    let response = await new Promise((resolve, reject) => {
        $post(store.settings.ajax_url + process.env.VUE_APP_SAVE, store.data)
            .then((response) => {
                resolve(response.data);
            })
            .catch(error => {
                reject(error);
                console.log(error);
            }).finally(function () {
            store.settings.save.disabled = false;
            store.settings.loading.global = false;
            if (store.translations.payment_paylater instanceof PayplugPaylater)
                validation_fields();
        })
    });

    store.settings.save.data = response.data;

}

export function validation_fields() {
    //validate oney_threshold fields
    oney_threshold_validation();

    function oney_threshold_validation() {
        let min = parseInt(store.data.oney_min_amounts);
        let max = parseInt(store.data.oney_max_amounts);

        const min_threshold = document.getElementsByName('oney_min_amounts')[0];
        const max_threshold = document.getElementsByName('oney_max_amounts')[0];
        const default_min_threshold = parseInt(min_threshold.min);
        const default_max_threshold = parseInt(max_threshold.max);

        if (min < default_min_threshold || max > default_max_threshold || min > max) {
            min_threshold.value = default_min_threshold;
            max_threshold.value = default_max_threshold;
            min_threshold.focus();
            max_threshold.focus();

            store.data.oney_min_amounts = default_min_threshold;
            store.data.oney_max_amounts = default_max_threshold;
        }
    }

}

export function populate(data) {
    store.translations.header = new PayplugHeader(data.header);
    store.translations.login = new PayplugLogin(data.login);
    store.translations.subscribe = new PayplugSubscribe(data.subscribe);
    store.translations.logged = new PayplugLogged(data.logged, data.payplug_wooc_settings);
    store.translations.payment_paylater = new PayplugPaylater(data.payment_paylater);
    // compatability with PSPL plugin
    if (typeof data.payment_methods !== "undefined") {
        store.translations.payment_methods = new PayplugPaymentMethods(data.payment_methods);
    }

    store.translations.status = new PayplugStatus(data.status);
    store.translations.debug = new PayplugDebugMode(data.status);
    store.translations.footer = new PayplugFooter(data.footer);
}

export function populate_status(data) {
    store.translations.status = new PayplugStatus(data);
}

export function populate_subscribe(data) {
    store.translations.subscribe = new PayplugSubscribe(data);
}

function request_payment_method_permissions($payment_method) {

    store.settings.save.disabled = true;
    return new Promise((resolve, reject) => {
        $post(
            $payment_method,
            {"env": store.data.payplug_sandbox}
        )
            .then((response) => {
                resolve(response.data);
            })
            .catch(error => {
                reject(error);
                console.log(error);
            }).finally(function () {
            store.settings.save.disabled = false;
        })
    });

}

export function getDataE2e(optionsData, value) {
    return (value) ? optionsData + String(value).replace(value[0], value[0]?.toUpperCase()) : null
}


