<template>
	<transition name="slide-fade">

		<div class="payplugUIModal" :id="identifier" data-e2e-name="popinAlert">
			<label class="_overlay" @click="closeModal"></label>
			<div class="_modal" :class="(modalTitle) ? '' : ' -noTitle '">
				<label for="modalTriggered" class="_close" @click="closeModal">
					<Icon iconName="close"/>
				</label>
				<div v-if="modalTitle" class="_header">{{ modalTitle }}</div>
				<div v-if="modalContent" class="_body" v-html="modalContent"></div>
				<slot v-else></slot>
				<div class="_footer" v-if="modalClose.length > 0">
					<CloseButton :button-text="modalClose" v-on:click="closeModal" dataE2eName="submit"/>
				</div>
			</div>
		</div>
	</transition>
</template>

<script>
import Icon from "../atoms/AtomIcon";
import {v4 as uuidv4} from 'uuid';
import CloseButton from "../atoms/AtomButton";
import {store} from "@/store/store";

export default {
	name: "MoleculeModal",
	components: {Icon, CloseButton},
	props: {
		modalTitle: {
			type: [Object, String],
			required: false
		},
		modalContent: {
			type: String,
			required: false,
			default: ""
		},
		modalClose: {
			type: [Object, String],
			required: false,
			default: ""
		}
	},
	data: () => {
		return {
			identifier: uuidv4(),
			store: store
		}
	},
	methods: {
		showModal() {
			document.getElementById(this.$data.identifier).classList.add("_show");
		},
		closeModal() {
			document.getElementById(this.$data.identifier).classList.remove("_show");
			store.error_inactive = false;
			store.error.login = null;
		}
	}
}
</script>

