<template>
  <div class="payplugUISwitch">
    <label class="_switch"
           :data-e2e-name="(switchData == 'oney_product_animation') ? 'payplug_oney_optimized' : switchData">
      <input type="checkbox"
             @change="updateStatus"
             :checked="this.switchChecked"
             :name="switchName"
             :data-e2e-name="(switchData == 'oney_product_animation') ? 'optimisedSwitch' : ''">
      <span class="_slider"></span>
      <span class="_label" data-enable-text="On" data-disable-text="Off"
            v-if="!this.switchGateways.includes(this.switchName)"></span>
    </label>
  </div>
</template>

<script>
import {store} from "@/store/store";
import {
  $enableAmericanExpress,
  $enableApplePay,
  $enableBancontact,
  $enableDeferred,
  $enableInstallment,
  $enableOneClick,
  $enableOney,
  $enableSatispay,
  $enableMybank,
  $enableSofort,
  $enableGiropay,
  $enableIdeal
} from "@/store/ajax";

export default {
  name: "AtomSwitch",
  props: {
    switchName: {
      type: String,
      required: false
    },
    switchChecked: {
      type: [Boolean, String],
      required: false
    },
    switchData: {
      type: String,
      required: false
    },
  },

  data() {
    return {
      store: store,
      switchGateways: [
        'oney',
        'applepay',
        'american_express',
        'bancontact',
        'standard',
        'satispay',
        'mybank',
        'sofort',
        'giropay',
        'ideal',
      ],
    }
  },
  created() {
    this.store.storeValue("enable_" + this.switchName, this.switchChecked || false)
  },
  methods: {
    async updateStatus(e) {
      this.store.storeValue("enable_" + this.switchName, e.target.checked);
      var response = null;

		//TODO:: make this as generic methods
		//TODO:: the enablefunction we five the e.target.getAttribute('name') and let the server side handle with the check

      switch (this.switchName) {
        case "standard":
          break;
        case "one_click":
          response = await $enableOneClick(e.target.checked);
          if (!response.success) {
            this.uncheck(e);
            store.error.payment_methods = response.data;
          }
          break;
        case "applepay":
          response = await $enableApplePay(e.target.checked);
          if (!response.success) {
            this.uncheck(e);
            store.error.payment_methods = response.data;
          }

          break;
        case "bancontact":
          response = await $enableBancontact(e.target.checked);
          if (!response.success) {
            this.uncheck(e);
            store.error.payment_methods = response.data;
          }
          break;
        case "american_express":
          response = await $enableAmericanExpress(e.target.checked);
          if (!response.success) {
            this.uncheck(e);
            store.error.payment_methods = response.data;
          }
          break;
        case "oney":
          response = await $enableOney(e.target.checked);
          if (!response.success) {
            this.uncheck(e);
            store.error.payment_methods = response.data;
          }
          break;
        case "payplug_inst":
          response = await $enableInstallment(e.target.checked);
          if (!response.success) {
            document.getElementById("payplug_inst_mode").classList.add("-disabled");
            document.getElementById("payplug_inst_min_amount").disabled = true;
            this.uncheck(e);
            store.error.payment_methods = response.data;
          } else {
            document.getElementById("payplug_inst_mode").classList.remove("-disabled");
            document.getElementById("payplug_inst_min_amount").disabled = false;
          }
          break;
        case "payplug_deferred":
          response = await $enableDeferred(e.target.checked);
          if (!response.success) {
            document.getElementById("payplug_deferred_state").classList.add("-disabled");
            this.uncheck(e);
            store.error.payment_methods = response.data;
          } else {
            document.getElementById("payplug_deferred_state").classList.remove("-disabled");
          }
          break;
        case "satispay":
          response = await $enableSatispay(e.target.checked);
          if (!response.success) {
            this.uncheck(e);
            store.error.payment_methods = response.data;
          }
          break;
        case "mybank":
          response = await $enableMybank(e.target.checked);
          if (!response.success) {
            this.uncheck(e);
            store.error.payment_methods = response.data;
          }
          break;
        case "sofort":
          response = await $enableSofort(e.target.checked);
          if (!response.success) {
            this.uncheck(e);
            store.error.payment_methods = response.data;
          }
          break;
        case "giropay":
          response = await $enableGiropay(e.target.checked);
          if (!response.success) {
            this.uncheck(e);
            store.error.payment_methods = response.data;
          }
          break;
        case "ideal":
          response = await $enableIdeal(e.target.checked);
          if (!response.success) {
            this.uncheck(e);
            store.error.payment_methods = response.data;
          }
          break;

        default:
          return null;
          // eslint-disable-next-line no-unreachable
          break;
      }
    },
    uncheck(e) {
      let that = this;
      //animation uncheck
      setTimeout(function () {
        that.store.storeValue("enable_" + that.switchName, false || false)
        e.target.checked = false
      }, 500)
    }
  }
}
</script>

