<template>
  <MoleculeAlert alertType="error"
                 :alertTitle="inactive_account.error.title"
                 :alertContent="inactive_account.error.description"
                 alertIcon="timer_error"
                 :alertClose="true"
                 v-if="inactive_modal.inactive && error_inactive_alert"
  />
  <MoleculeAlert alertType="warning"
                 :alertTitle="inactive_account.warning.title"
                 :alertContent="inactive_account.warning.description"
                 alertIcon="lightbulb_orange"
                 v-if="inactive_modal.inactive"
  />
  <MoleculeAlert alertType="success"
                 :alertTitle="inactive_account.success.title"
                 :alertContent="inactive_account.success.description"
                 alertIcon="check_24px"
                 :alertClose="true"
                 v-if="active_alert"
  />
  <section class="payplugUiBlock generalBlock -logged" v-bind:class="disabled ? '-disabled' : null" data-e2e-name="generalLogged">
	<AtomLoader v-if="loading" class="component-wrapper" />
    <BlockHeader :blockHeaderTitle="title" :blockHeaderDescription="description" />
    <div class="_content" v-bind:class = "(loading)?'blurred':''">
      <div class="_user">
        <div class="_userInformations">
          {{ email }}
          <Tooltip data-e2e-name="payplugTooltipLogout">
            <PayplugButton :buttonName="logout"
                             :buttonText="logout"
                             @click.prevent="doLogout"
                             data-e2e-name="payplugLogout"/>
          </Tooltip>
        </div>
        <ButtonLink class="_userPortal -icon"
                    :buttonLinkText="link_access_portal_text"
                    :buttonLinkHref="link_access_portal_href"
                    buttonLinkIcon="link" />
      </div>
      <div class="_sandbox">
        <Title class="_sandboxTitle" :titleText="mode"/>
        <ParagraphAnchors class="_sandboxDescription -test" :paragraphAnchorsText="mode_description" :paragraphAnchorsLinks="[link_learn_more]" paragraphAnchorsData="faq" />
        <PayplugOptions v-if="options" class="_sandboxRadioButton" :optionsItems="options" :optionsName="options[0].name" optionsData="sandbox" />
      </div>
    </div>
    <Modal ref="inactiveModal" class="text-left" :class="error_inactive ? '_show' : ''"
           :modalTitle="inactive_modal.title">
      <Paragraph :paragraphText="inactive_modal.description"/>
      <br/>
      <InputLabel :inputLabelClass="'_password'" :inputLabelType="'password'" :inputLabelName="'payplug_password'" :inputLabelLabel="inactive_modal.password_label"  inputLabelData="payplugPassword"/>
      <br/><br/>
      <div class="row">
        <div class="col-5">
          <PayplugButton class="w-100" :buttonText="inactive_modal.cancel" @click.prevent="closeModlal" />
        </div>
        <div class="col-2"></div>
        <div class="col-5">
          <PayplugButton class="w-100" :buttonText="inactive_modal.ok" @click.prevent="refresh_keys" />
        </div>
      </div>
    </Modal>
    <Modal ref="errorModal" :modalContent="error_message" modalClose="Ok">
      <PayplugButton buttonText="Ok" @click.prevent="closeErrorModlal" />
    </Modal>
  </section>
</template>

<script>
import BlockHeader from "../molecules/MoleculeBlockHeader";
import Tooltip from "../molecules/MoleculeTooltip";
import PayplugButton from "../atoms/AtomButton";
import ButtonLink from "../atoms/AtomButtonLink";
import Title from "../atoms/AtomTitle";
import Anchor from "../atoms/AtomAnchor";
import PayplugOptions from "../atoms/AtomOptions";
import ParagraphAnchors from "@/components/molecules/MoleculeParagraphAnchors";
import {store} from "@/store/store";
import AtomLoader from "@/components/atoms/AtomLoader"
import {$logout, $refresh_keys} from "@/store/ajax";
import Modal from "@/components/molecules/MoleculeModal";
import Paragraph from "@/components/atoms/AtomParagraph";
import InputLabel from "@/components/molecules/MoleculeInputLabel";
import MoleculeAlert from "@/components/molecules/MoleculeAlert";

export default {
  name: "OrganismSectionLoggedIn",
  components: {
    PayplugButton, BlockHeader, Tooltip, ButtonLink, Anchor, PayplugOptions, Title, ParagraphAnchors, AtomLoader,
    Modal, Paragraph, InputLabel, MoleculeAlert
  },
  computed: {
    disabled: {
      get: () => {
        return store.translations.logged.can_be_disabled && parseInt(store.data.payplug_enable) != 1;
      }
    },
    title: {
      get: () => store.translations.logged.title
    },
    description: {
      get: () => store.translations.logged.description
    },
    email: {
      get: () => store.translations.logged.email
    },
    logout: {
      get: () => store.translations.logged.logout
    },
    link_access_portal_text: {
      get: () => store.translations.logged.link_access_portal.text
    },
    link_access_portal_href: {
      get: () => store.translations.logged.link_access_portal.url
    },
    mode: {
      get: () => store.translations.logged.mode
    },
    mode_description: {
      get: () => store.translations.logged.mode_description
    },
    link_learn_more: {
      get: () => store.translations.logged.link_learn_more
    },
    options: {
      get: () => store.translations.logged.options
    },
    loading: {
      get: () => store.settings.loading.logout
    },
    inactive_modal: {
      get: () => store.translations.logged.inactive_modal
    },
    error_inactive: {
      get: () => store.error_inactive
    },
    error_message() {
      if (store.error.refresh_keys !== null && typeof store.error.refresh_keys !== "undefined")
        this.showErrorModal();

      return store.error.refresh_keys
    },
    inactive_account: {
      get: () => store.translations.logged.inactive_account
    },
    error_inactive_alert: {
      get: () => store.error_inactive_alert
    },
    active_alert: {
      get: () => store.active_alert
    },
  },
  methods: {
    doLogout() {
      store.settings.loading.logout = true;
      $logout();
    },
    showModal() {
      this.$refs.inactiveModal.showModal();
    },
    showErrorModal() {
      this.$refs.errorModal.showModal();
    },
    closeModlal() {
      this.$refs.inactiveModal.closeModal();
    },
    closeErrorModlal() {
      this.$refs.errorModal.closeModal();
    },
    refresh_keys() {
      this.closeModlal();
      store.settings.loading.logout = true;
      $refresh_keys();
    },
    onClick() {
      document.getElementById("payplug_sandbox0").click();
    }
  },
  mounted() {
    if (document.getElementById("inactiveModalClick"))
      document.getElementById("inactiveModalClick").addEventListener('click', this.onClick);
  },
  unmounted() {
    if (document.getElementById("inactiveModalClick"))
      document.getElementById("inactiveModalClick").removeEventListener('click', this.onClick);
  }
}
</script>

