<template>
  <div class="bootstrap">
    <LandingPage />
  </div>
</template>

<script>
import NavBar from './components/organisms/NavBar';
import LandingPage from './pages/LandingPage';

export default {
	name: 'App',
	components: { NavBar, LandingPage },

	beforeMount() {
		if( typeof window.payplug_admin_config !== "undefined"){
			// eslint-disable-next-line no-undef
			__webpack_public_path__ = window.payplug_admin_config.img_path;
		}
	},
	mounted: function () {

		switch (process.env.VUE_APP_NAME){
			case "pspaylater": import('./assets/less/pspaylater.less');break;
			default: import('./assets/less/admin.less');break;
		}

	}
}
</script>

