<template>
  <div class="_inputs">
    <Paragraph
        :paragraphText="option.description_1"
        class="d-inline"/>
    <SelectBox
        v-if="installment_options"
        :class="'installmentMode'"
        :selectName="installment_options.name"
        :selectOptions="installment_options.options"
        :selectDisabled="installment_options.disabled"/>
    <Paragraph :paragraphText="option.text_from"/>
    <div
        v-if="installment_value"
        class="payplugUIInput installmentMinAmount -icon -iconEuro -disabled"
        :class="show_amount_error ? '-error' : null">
      <AtomInput
          :inputType="'number'"
          :inputName="installment_value.name"
          :id="installment_value.name"
          :inputValue="installment_value.value"
          :inputMin="installment_value.min"
          :inputMax="installment_value.max"
          :inputStep="installment_value.step"
          :inputDisabled="installment_value.disabled"
          @change.capture="emitChange"/>
    </div>
  </div>
  <div class="_statement" v-if="installment_value && show_amount_error">
    <div class="_icon installmentErrorIcon">
      <Icon iconName="error_alert"/>
    </div>
    {{ installment_value.out_of_bound_msg }}
  </div>
  <div style="margin-bottom: 8.5px;" v-if="option">
    <Paragraph
        :paragraphText="option.description_2"
        class="d-inline"/>
    <Anchor
        v-for="anchor in option.links"
        v-bind:key="anchor"
        :anchorHref="anchor.url"
        :anchorText="anchor.text"
        :anchorTitle="anchor.text"
        :anchorTarget="anchor.target"
        :anchorDataE2e="typeof anchor.data_e2e != 'undefined' ? anchor.data_e2e : null"/>
  </div>
  <TextAlert
      v-if="option?.notes"
      :textAlertText="option?.notes?.description"
      :textAlertType="option?.notes?.type"/>
</template>

<script>
import Paragraph from "@/components/atoms/AtomParagraph";
import Anchor from "@/components/atoms/AtomAnchor";
import SelectBox from "@/components/atoms/AtomSelect";
import AtomInput from "@/components/atoms/AtomInput";
import TextAlert from "@/components/atoms/AtomTextAlert";
import Icon from "@/components/atoms/AtomIcon";
import {store} from "@/store/store";

export default {
  name: "MoleculeFractionalPayment",
  components: {Paragraph, Anchor, SelectBox, AtomInput, TextAlert, Icon},
  props: {
    option: {
      type: Object,
      required: true
    }
  },
  created() {
    this.init()
  },
  data() {
    return {
      store: store,
      installment_options: false,
      installment_value: false,
      show_amount_error: false
    }
  },
  methods: {
    init() {
      this.option.options.map((option) => {
        switch (option.name) {
          case 'payplug_inst_mode':
            this.installment_options = option;
            break;
          case 'payplug_inst_min_amount':
            this.installment_value = option;
            break;
        }
      });
    },
    emitChange(e) {
      if (this.store.data.enable_payplug_inst) {
        let amount = parseInt(e.target.value);
        this.show_amount_error = isNaN(amount)
            || parseInt(amount) < this.installment_value.min
            || parseInt(amount) > this.installment_value.max;

        this.store.storeValue('has_installment_error', this.show_amount_error)
      }
    }
  }
}
</script>

