<template>
  <Switch v-show="showSwitch" :switchName="name" :switchChecked="(checked || !showSwitch)? true : null" switchData="paymentMethod_oney"/>
  <PaylaterHeader :description="oney_description" :title="oney_title" :links="links" />
  <PaylaterInformations v-show="store.data.enable_oney" />
</template>

<script>
import Switch from "@/components/atoms/AtomSwitch";
import PaylaterHeader from "@/components/organisms/Paylater/OrganismPaylaterHeader";
import PaylaterInformations from "@/components/organisms/Paylater/OrganismPaylaterInformations";
import {store} from "@/store/store";
import PayplugPaymentMethods from "@/classes/PayplugPaymentMethods.vue";

export default  {
  name: "OrganismPaylaterFull",
  components:{ Switch, PaylaterHeader, PaylaterInformations },
  computed: {
    name: {
      get: () => store.translations.payment_paylater.name
    },
    checked() {
		return store.data.enable_oney;
    },
    oney_title: {
      get: () => store.translations.payment_paylater.oney_title
    },
    oney_description: {
      get: () => store.translations.payment_paylater.oney_description
    },
    links: {
      get: () => store.translations.payment_paylater.links
    },
	showSwitch: function() { return  ( store.translations.payment_methods instanceof PayplugPaymentMethods  ) },
  },
  data(){
    return {
      store: store,
    }
  },
}
</script>

