<script>

import {store} from "@/store/store";

export default class PayplugStatus {

	title = null;
	description = null;
	checked = null;
	name = "enable_debug"

	constructor(data) {

		const mode = store.manageMode();

		if (typeof data === "undefined")
			return;

		if(typeof data.debug === "undefined" || typeof data.debug[mode] === "undefined")
			return;

		this.title = data.debug[mode].title;
		this.description = data.debug[mode].description;
		this.checked = data.enable_debug_check;

	}

}

</script>
